.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

@font-face {
  font-family: "kurdishfont";
  src: url("../../fonts/droidkufi/DroidKufi-Regular.woff") format("truetype");
}

@font-face {
  font-family: "englishfont";
  /* src: url("../../fonts/KGoran.ttf") format("truetype"); */
  src: url("../../fonts/Roboto-Medium.ttf") format("truetype");
}

* {
  font-size: 0.975em;
  /* font-family: "kurdishfont" !important; */
}

input:focus {
  outline: none !important;
}

button:focus {
  outline: none !important;
}

ul li::before {
  content: "â€¢";
  font-weight: bold;
  font-size: 30px;
  color: #7d164a;
}

a {
  color: white;
}

h2 {
  font-size: 1.8em !important;
}

h3 {
  font-size: 1.4em !important;
}

.bb-button {
  width: 140px;
  background-color: #881a51;
  border-radius: 40px;
  outline: none;
  border: none;
  color: white;
  padding: 12px 16px;
  transition: all 0.1s;
  margin: 0px 8px;
  text-align: center;
}

.bb-button:hover {
  transition: all 0.3s;
  background-color: #7d164a;
  -webkit-box-shadow: -7px 7px 26px -9px #7d164a;
  -moz-box-shadow: -7px 7px 26px -9px #7d164a;
  box-shadow: -7px 7px 26px -9px #7d164a;
}

.bb-button:active {
  background-color: #7d164a;
}

.bb-button-ghost {
  width: 140px;
  background-color: white;
  border-radius: 40px;
  outline: none;
  border: 2px solid #881a51;
  color: #881a51;
  padding: 12px 16px;
  transition: all 0.1s;
  margin: 0px 8px;
  text-align: center;
  transition: all 0.1s;
}
.bb-button-ghost:hover {
  background-color: #881a51;
  transition: all 0.3s;
  color: white;
}

.slider {
  /* height: auto !important; */
}

.disable-select {
  user-select: none; /* supported by Chrome and Opera */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
}

.bb-tab-button {
  outline: none;
  border: none;
  color: #881a51;
  background-color: white;
  /* border-right: 1px solid #881a51; */
  padding: 8px 12px;
  box-sizing: border-box;
  transition: all 0.2s;
  border-radius: 4px;
}

.bb-tab-button:hover {
  transition: all 0.2s;
  color: white;
  background-color: #881a51;
}

.bb-tab-button-active {
  color: white !important;
  background-color: #881a51 !important;
}

.custom-radio .custom-control-input:checked ~ .custom-control-label::before {
  background-color: #881a51 !important;
}

.custom-control-input:checked ~ .custom-control-label::before {
  background-color: #881a51 !important;
}

.pp-header {
  margin-bottom: 0px;
}

.pp-c4 {
  font-weight: bold;
  font-size: 16px;
  line-height: 66px !important;
}
